@use 'sass:map';
@use '../../common/scss/abstracts/variables';
@use '../../common/scss/abstracts/functions';
@use '../../common/scss/abstracts/mixins';

.samis {
    &_visual {
        width: 100%;
        height: 400px;
        background: #eee url(../../dig/images/sam/img_samWhat_top.png) no-repeat center top;
    }

    &_tabMenu {
        display: flex;
        align-items: center;
        justify-content: center;

        li {
            width: 240px;
            height: 72px;

            button {
                width: 100%;
                height: 100%;
                font-size: 16px;
                color: #767676;
                cursor: pointer;

                &.current {
                    position: relative;
                    font-weight: 600;
                    color: #000;

                    &:after {
                        position: absolute;
                        left: 50%;
                        bottom: 0;
                        width: 100%;
                        height: 3px;
                        background-color: #000;
                        transform: translateX(-50%);
                        animation: sort .1s linear;
                        content: "";
                    }
                }
            }
        }
    }

}

@keyframes sort {
    from {width: 0}
    to {width: 100%}
}
@keyframes slideTop {
    from {background-position-y: 0}
    to {background-position-y: -638px}
}
@keyframes visualTxt {
    0% {transform:translate3d(0,50%,0);opacity:0}
    to {transform:translateZ(0);opacity:1}
}
